import React, { Component } from "react";
import { Row, Col, Container, Form, Card, Button } from "react-bootstrap";
import { animateScroll as scroll } from "react-scroll";

export default class ContactMe extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "",
      validated: false,
      email: "",
      name: "",
      number: "",
      message: "",
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.scrollToTop = this.scrollToTop.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  }

  handleSubmit = (event) => {
    const formInfo = event.currentTarget;
    if (formInfo.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
      this.setState({validated:true})
      return
    }
    this.setState({validated:false})

    const data = {
      email: this.state.email,
      name: this.state.name,
      number: this.state.number,
      message: this.state.message,
    };
    const xhr = new XMLHttpRequest();
    xhr.open("POST", "https://formspree.io/xyynleno");
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        this.setState({
          status: "SUCCESS",
          validated: false,
          email: "",
          name: "",
          number: "",
          message: "",
        });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  };

  scrollToTop() {
    scroll.scrollToTop();
  }

  render() {

    return (
      <div style={{ marginBottom: 50 }} id="contactMeDiv">
        <Container>
          <hr />
          <h2>Contact Me</h2>
          <Row>
            <Col lg={12}>
              <Card className="text-left">
                <Card.Header
                  style={{ color: "white", backgroundColor: "#0e1e25" }}
                >
                  Message Me
                </Card.Header>
                <Card.Body>
                  <Form
                    noValidate
                    validated={this.state.validated}
                    onSubmit={this.handleSubmit}
                  >
                    <Form.Group controlId="form.emailAddress">
                      <Form.Label>Email Address:</Form.Label>
                      <Form.Control
                        type="email"
                        placeholder="name@example.com"
                        name="email"
                        value={this.state.email}
                        onChange={this.handleInputChange}
                        
                        required
                      />
                      <Form.Control.Feedback type="invalid">
                        Please provide a valid email.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="form.name">
                      <Form.Label>Name:</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="John Doe"
                        name="name"
                        value={this.state.name}
                        onChange={this.handleInputChange}
                        minLength="2" 
                        required
                      />

                      <Form.Control.Feedback type="invalid">
                        Please enter a valid name.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="form.number">
                      <Form.Label>Phone Number:</Form.Label>
                      <Form.Control
                        type="tel"
                        placeholder="555-555-5555"
                        name="number"
                        value={this.state.number}
                        onChange={this.handleInputChange}
                        minLength="7"
                        required
                      />
                      <Form.Control.Feedback type="invalid">
                        Please enter a valid number.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group controlId="form.message">
                      <Form.Label>Message:</Form.Label>
                      <Form.Control
                        as="textarea"
                        rows="3"
                        name="message"
                        minLength="10" 
                        value={this.state.message}
                        onChange={this.handleInputChange}
                        required
                      />
                      <Form.Control.Feedback type="invalid">
                        Please enter a valid message.
                      </Form.Control.Feedback>
                    </Form.Group>
                    {this.state.status === "SUCCESS" ? (
                      <p>Thanks!</p>
                    ) : (
                      <Button type="submit" variant="primary" size="lg" block>
                        Submit
                      </Button>
                    )}
                    {this.state.status === "ERROR" && <p>Ooops! There was an error.</p>}
                  </Form>
                </Card.Body>
                <Card.Footer className="text-center">
                  <a href="mailto: stebrown2018@gmail.com">
                    <i className="fas fa-envelope" style={{ fontSize: 30 }} />
                  </a>
                  &nbsp; &nbsp;
                  <a
                    href="https://www.linkedin.com/in/stephon-brown-87779572/"
                    target="#"
                  >
                    <i className="fab fa-linkedin" style={{ fontSize: 30 }} />
                  </a>{" "}
                  &nbsp; &nbsp;
                  <a href="https://github.com/StephonBrown" target="#">
                    <i className="fab fa-github" style={{ fontSize: 30 }} />
                  </a>{" "}
                  &nbsp; &nbsp;
                  <a href="https://codepen.io/Tatermate09/" target="#">
                    <i className="fab fa-codepen" style={{ fontSize: 30 }} />
                  </a>{" "}
                  &nbsp; &nbsp;
                  <a
                    href="https://www.freecodecamp.org/stephonbrown"
                    target="#"
                  >
                    <i
                      className="fab fa-free-code-camp"
                      style={{ fontSize: 30 }}
                    />
                  </a>
                </Card.Footer>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col
              md={12}
              xs={12}
              style={{ paddingTop: 25, textAlign: "center" }}
            >
              <p>Return to Top</p>
              <a href="/" onClick={this.scrollToTop}>
                <i
                  id="returnArrow"
                  className="fas fa-arrow-circle-up blink"
                  style={{ textAlign: "center", fontSize: 30 }}
                ></i>
              </a>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
